*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: "Hiragino-SansW5", sans-serif;
}

html {
  box-sizing: border-box;
  font-size: 62.5%; // 10px/16px = 62.5% -> 1rem = 10px //setting rem
}

@import "./Component/C_Navbar.scss";
@import "./Layout/Upload.scss";
@import "./Layout/Convert.scss";
@import "./Layout/Login.scss";
@import "./Layout/Show.scss";
@import "./Component/C_QuestionCard.scss";
